<template>
    <div>
        <van-nav-bar title="其他费用查询"/>
         <van-list
        v-model="loading"
        :finished="finished"
        :immediate-check="false"
        finished-text="没有更多了"
        @load="onLoad"
        :offset="10"
      >
            <van-cell-group v-for="(item,index) in otherlist" :key="index">
            <van-cell title="房间号" :value="item.room_num" />
            <van-cell title="费用类型" :value="item.cost_type" />
            <van-cell title="名字" :value="item.person_name" />
            <van-cell title="费用金额" :value="item.cost" />
            <van-cell title="时间" :value="item.meter_date" />
            </van-cell-group>
         </van-list>
    </div>
</template>

<script>
import { apiotherlist } from "@/api/index";
export default {
    name:'otherCost',
    data() {
        return {
            loading: false,
            finished: false,
            page: 1, //请求第几页
            pageSize: 5, //每页请求的数量
            total: 0, //总共的数据条数
            otherlist: []
        }
    },
    methods: {
        // 上拉加载
    onLoad() {
      this.page++;
      this.getList();
    },
    //获取列表
    getList() {
      let params = {
        page: this.page,
        pageSize: this.pageSize
      };
      apiotherlist(params).then(res => {
        this.loading = false;
        debugger
        this.total = res.data.count; 
        let rows = res.data.data
        if (res.data.data == null || rows.length === 0) {
          // 加载结束
          this.finished = true;
          return;
        }
        this.otherlist = this.otherlist.concat(rows);
         this.isLoading = false;
         if (this.otherlist.length >= this.total) {
            this.finished = true;
         }
      });
    },
    },
    created() {
    this.getList();
  },
}
</script>

<style>

</style>


